body {
    background-color: #121212;
    color: #ffffff;
    font-family: 'Arial', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
  }
  
  .quiz-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    max-width: 500px;
    text-align: center;
  }
  
  .quiz-box, .result-box {
    background-color: #1e1e1e;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    width: 100%;
  }
  
  .quiz-question {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  .quiz-button {
    width: 100%;
    background-color: #ff4081;
    border: none;
    color: white;
    padding: 12px;
    margin: 8px 0;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 8px;
    transition: background 0.3s, transform 0.2s ease-in-out;
    position: relative;
    overflow: hidden;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .quiz-button:hover {
    background-color: #e91e63;
    transform: scale(1.05);
    box-shadow: 0px 4px 15px rgba(233, 30, 99, 0.5);
  }
  
  .quiz-button:active {
    transform: scale(0.95);
  }
  
  .animated {
    animation: fadeInUp 0.6s ease-out;
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .result-title {
    font-size: 1.8rem;
    font-weight: bold;
  }
  
  .result-text {
    margin: 10px 0;
  }
  
  @media (max-width: 600px) {
    .quiz-box, .result-box {
      width: 100%;
      padding: 15px;
    }
    .quiz-question {
      font-size: 1.2rem;
    }
    .quiz-button {
      font-size: 0.9rem;
    }
  }
  